.upper-item {
    width: 350px;
    display: flex;
    flex-direction: column;
    height: 350px;
    align-items: center;
    border-radius: 14px;
}
  
    .image {
      width: 100%;
      height: 95%;
      background-size: cover;
      background-position: center;
      margin-bottom: 5px;
      border-radius: 14px;
    }
  
    .upper-footer {
      width: 100%;
      height: 5%;
      display: flex;
      justify-content: space-between;
      font-size: 18px;
    }
  
      .name {
        width: 90%;
        margin-bottom: 15px;
        color: white;
        text-decoration: underline none;
        text-align: center;
      }
      .name:hover{
        color: #f98817;
      }
  
    